
.index-main .content > .content-warp {
  width: 1140px;
  padding: 0 20px 20px;
  background-color: rgba(255, 255, 255, 0.9);
}
.index-main .superiority {
  height: 194px;
  background: #fefefe;
  padding: 63px 0 49px 0;
}
.index-main .superiority .superiority-item {
    cursor: pointer;
    border-left: 1px solid #e5e5e5;
    float: left;
    color: #7f7f7f;
    width: 360px;
    padding: 0 75px 0 31px;
    -webkit-box-sizing: border-box;
       -moz-box-sizing: border-box;
            box-sizing: border-box;
    font-weight: lighter;
    white-space: nowrap;
    font-size: 22px;
}
.index-main .superiority .superiority-item:first-child {
      border: none;
}
.index-main .superiority .superiority-item:hover {
      color: #ed7e12;
}
.index-main .superiority .superiority-item:hover .active {
      display: inherit;
}
.index-main .superiority .superiority-item:hover img {
      display: none;
}
.index-main .superiority .superiority-item .active {
      display: none;
}
.index-main .superiority .superiority-item img {
      float: left;
}
.index-main .superiority .superiority-item h1 {
      margin-left: 102px;
      font-weight: lighter;
}
.index-main .superiority .superiority-item small {
      display: block;
      margin-top: 14px;
      font-size: 18px;
}
.index-main .award {
  height: 167px;
  background: #f4f4f4;
  padding-top: 13px;
  position: relative;
}
.index-main .award .rt-img {
    position: absolute;
    right: -17px;
    top: -42px;
}



