
.back-Call {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 98;
  background-color: rgba(0, 0, 0, 0.7);
  text-align: center;
}
.back-Call .back-call-content {
    position: relative;
    background-color: #fff;
    display: inline-block;
    -webkit-border-radius: 15px;
       -moz-border-radius: 15px;
            border-radius: 15px;
    margin-top: 10%;
}
.back-Call .back-call-content .inputbox {
      width: 550px;
      min-height: 45px;
      margin: 0 auto 12px;
      clear: both;
      position: relative;
}
.back-Call .back-call-content .inputbox .label {
        font-size: 16px;
        color: #666666;
        float: left;
        height: 40px;
        line-height: 45px;
        min-width: 112px;
        text-align: center;
}
.back-Call .back-call-content .inputbox input {
        width: 420px;
        height: 40px;
        float: left;
        -webkit-border-radius: 4px;
           -moz-border-radius: 4px;
                border-radius: 4px;
        outline: none;
        background: #f8f8f8;
        border: solid 1px #cacaca;
        font-size: 14px;
        text-indent: 15px;
        color: #666;
}
.back-Call .back-call-content h1 {
      font-size: 24px;
      color: #282828;
      margin-bottom: 15px;
      margin-top: 15px;
      text-align: center;
      line-height: 31px;
      font-weight: normal;
}
.back-Call .back-call-content .formbtn {
      margin: 10px auto 20px;
      display: block;
      width: 100px;
      cursor: pointer;
      letter-spacing: 4px;
      height: 40px;
      line-height: 40px;
      font-size: 16px;
      background: #1e327e;
      -webkit-border-radius: 4px;
         -moz-border-radius: 4px;
              border-radius: 4px;
      color: #fff;
      text-align: center;
      font-weight: bold;
}
.back-Call .closeBtn {
    display: block;
    position: absolute;
    top: 10px;
    right: 16px;
    cursor: pointer;
    color: #ccc;
    z-index: 1;
}
.back-Call .closeBtn .iconfont {
      font-size: 30px;
}



