
div.footer .footer-website {
  background: #1f1f1f;
  text-align: center;
  height: 79px;
  line-height: 79px;
  color: #fff;
}
div.footer .footer-website a {
  padding: 0 40px;
  border-left: #5e5e5e solid 1px;
}
div.footer .footer-website a.bdnone {
  border: none;
}
div.footer .footer-website img {
  margin-top: -35px;
  width: 70px;
}
div.footer .footer-btm {
  background: #2d2d2d;
  height: 140px;
}
div.footer .friend-link {
  text-align: center;
  line-height: 87px;
}
div.footer .friend-link a {
  margin: 0 5px;
}
div.footer .footer-tips {
  color: #efefef;
  line-height: 31px;
}
div.footer .footer-tips p {
  display: inline-block;
}
div.footer .footer-browser a {
  display: inline-block;
  width: 31px;
  height: 31px;
  background-image: url(./images/footer-browser.png);
  background-repeat: no-repeat;
  text-indent: -9999px;
  margin: 0 2px;
}
div.footer .footer-browser .chrome-icon {
  background-position: -20px -156px;
}
div.footer .footer-browser .firefox-icon {
  background-position: -20px -88px;
}
div.footer .footer-browser .safari-icon {
  background-position: -20px -224px;
}
div.footer .footer-browser .ie-icon {
  background-position: -20px -20px;
}



