
.deposit-tips {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
  -webkit-box-shadow: 1px 1px 50px rgba(0, 0, 0, 0.3);
     -moz-box-shadow: 1px 1px 50px rgba(0, 0, 0, 0.3);
          box-shadow: 1px 1px 50px rgba(0, 0, 0, 0.3);
  z-index: 200;
  text-align: center;
}
.deposit-tips::before {
    content: '';
    height: 100%;
    display: inline-block;
    vertical-align: middle;
}
.deposit-tips .deposit-tips-content {
    width: 600px;
    display: inline-block;
    vertical-align: middle;
    -webkit-border-radius: 2px;
       -moz-border-radius: 2px;
            border-radius: 2px;
    background-color: #fff;
    color: #666666;
    position: relative;
    text-align: left;
}
.deposit-tips .deposit-tips-content .iclose {
      font-size: 16px;
      color: #2E2D3B;
      cursor: pointer;
      float: right;
}
.deposit-tips .deposit-tips-content .title {
      padding: 0 10px 0 20px;
      height: 42px;
      line-height: 42px;
      border-bottom: 1px solid #eee;
      font-size: 14px;
      color: #333;
      overflow: hidden;
      background-color: #F8F8F8;
      -webkit-border-radius: 2px 2px 0 0;
         -moz-border-radius: 2px 2px 0 0;
              border-radius: 2px 2px 0 0;
}
.deposit-tips .deposit-tips-content .content-info {
      position: relative;
      padding: 20px;
      line-height: 24px;
      word-break: break-all;
      overflow: hidden;
      font-size: 14px;
      overflow-x: hidden;
      overflow-y: auto;
}
.deposit-tips .deposit-tips-content .content-info .conent {
        font-size: 16px;
        border: 1px solid #1e327e;
        margin: 10px;
        padding: 10px;
}
.deposit-tips .deposit-tips-content .btn-group {
      text-align: right;
      padding: 0 10px 12px;
      pointer-events: auto;
}
.deposit-tips .deposit-tips-content .btn-group .blue-btn {
        height: 28px;
        line-height: 28px;
        margin: 0 6px;
        padding: 0 15px;
        border: 1px solid #4898d5;
        background-color: #2e8ded;
        color: #fff;
        -webkit-border-radius: 2px;
           -moz-border-radius: 2px;
                border-radius: 2px;
        font-weight: 400;
        cursor: pointer;
        text-decoration: none;
        display: inline-block;
}
.deposit-tips .deposit-tips-content .btn-group .blue-btn:hover {
          background-color: #1e327e;
}



