
.vip-step {
  padding: 45px 20px;
}
.vip-step .titleT {
    height: 42px;
    line-height: 42px;
    color: #000;
    text-align: left;
    border-bottom: 1px solid #1e327e;
    margin-bottom: 10px;
    font-size: 18px;
}
.vip-step .titleT span {
      /*background: #1e327e;*/
      display: block;
      width: 206px;
}
.vip-step table {
    width: 100%;
    border-spacing: 0;
    font-size: 14px;
    text-align: center;
    border-collapse: collapse;
}
.vip-step table th {
      background: #1e327e;
      padding: 10px 20px;
      font-weight: 100;
      color: #fff;
      height: 50px;
}
.vip-step table td {
      padding: 10px 20px;
      border: 1px solid #b8b8b8;
}

