
.recommend-firend .moneywrap {
  margin: 0 auto 20px;
  text-align: center;
  color: #1e327e;
  cursor: pointer;
}
.recommend-firend .moneywrap .moneybox {
    border: 1px solid #1e327e;
    background: #1e327e;
    color: #fff;
    width: 180px;
    height: 67px;
    line-height: 1.4;
    padding-top: 10px;
    -webkit-border-radius: 5px;
       -moz-border-radius: 5px;
            border-radius: 5px;
    float: none !important;
    display: inline-block;
    margin: 0 5px;
    margin-bottom: 10px;
}
.recommend-firend .moneywrap .moneybox h2 {
      font-weight: normal;
      font-size: 18px;
      margin: 0 auto 5px;
      text-align: center;
}
.recommend-firend .moneywrap .moneybox .date {
      width: 100%;
      text-align: center;
      display: block;
      font-size: 12px;
}
.recommend-firend .bottom-tip {
  margin: 50px 0;
}
.recommend-firend .my-link-copy {
  cursor: pointer;
  padding: 15px;
  border: 1px dashed #f66;
  -webkit-border-radius: 5px;
     -moz-border-radius: 5px;
          border-radius: 5px;
  line-height: 24px;
  color: #999;
  background: #fff;
  width: 300px;
  margin: 0 auto;
}



