
.down-page {
  background: url(~images/banner/phone-banner.jpg) top center no-repeat;
  padding-top: 257px;
}
.content {
  padding-bottom: 75px;
}
.content .content-warp {
    width: 1140px;
    margin: 0 auto;
    background: url(~images/download/bg.jpg) no-repeat #fff;
    -webkit-box-shadow: 0px 2px 4px #dde2e7;
       -moz-box-shadow: 0px 2px 4px #dde2e7;
            box-shadow: 0px 2px 4px #dde2e7;
    min-height: 948px;
}
.content .content-warp .hauto {
      height: auto !important;
}
.content .content-warp .banner2 {
      position: relative;
      margin: 0 auto;
      max-width: 1920px;
      overflow: hidden;
}
.content .content-warp .banner2 img {
      position: relative;
      display: block;
      width: 1920px;
      margin-left: -960px;
      left: 50%;
}
.content .content-warp .tab-ment {
      padding: 50px 0 20px;
      margin: 0 50px;
      border-bottom: 1px solid #e6e6e6;
      text-align: center;
}
.content .content-warp .tab-ment span {
        display: inline-block;
        height: 32px;
        line-height: 32px;
        text-align: center;
        padding: 0 26px;
        -webkit-border-radius: 16px;
           -moz-border-radius: 16px;
                border-radius: 16px;
        background: #d7d7d7;
        color: #333333;
        margin: 0 10px;
        margin-bottom: 10px;
        font-size: 16px;
        cursor: pointer;
}
.content .content-warp .tab-ment span.active {
          background: #ed7e12;
          color: #fff;
}
.content .content-warp .tab-ment span.active i {
            background-position: center -24px !important;
}
.content .content-warp .tab-ment span.active i.i5 {
            background-position: center -25px !important;
}
.content .content-warp .content-info {
      overflow: hidden;
      margin: 0 18px;
      padding: 60px 134px;
      background: none;
      padding-right: 0;
}
.content .content-warp .content-info div {
        width: 480px;
        float: left;
        font-size: 14px;
        color: #666666;
        height: 480px;
}
.content .content-warp .content-info div h3 {
          padding-left: 40px;
          line-height: 40px;
          font-size: 16px;
          color: #333333;
          font-weight: bold;
          margin-bottom: 15px;
}
.content .content-warp .content-info div h3.i1 {
            background: url(~images/download/01.png) no-repeat left top;
            line-height: 45px;
}
.content .content-warp .content-info div h3.i2 {
            background: url(~images/download/02.png) no-repeat left top;
}
.content .content-warp .content-info div h3.i3 {
            background: url(~images/download/03.png) no-repeat left top;
}
.content .content-warp .content-info div p {
          margin-bottom: 20px;
}
.content .content-warp .content-info div img {
          -webkit-box-shadow: 0px 2px 4px #dde2e7;
             -moz-box-shadow: 0px 2px 4px #dde2e7;
                  box-shadow: 0px 2px 4px #dde2e7;
          margin-bottom: 20px;
}
.content .content-warp .content-info div input, .content .content-warp .content-info div .url {
          width: 390px;
          height: 30px;
          -webkit-box-sizing: border-box;
             -moz-box-sizing: border-box;
                  box-sizing: border-box;
          padding-left: 20px;
          color: #333333;
          font-weight: bold;
          font-size: 16px;
          background: #e0e0e0;
          -webkit-border-radius: 15px;
             -moz-border-radius: 15px;
                  border-radius: 15px;
          margin-top: 10px;
}
.content .content-warp .content-info div a {
          display: inline-block;
          padding: 7px 30px;
          color: #fff;
          background: #ed7e12;
          -webkit-border-radius: 30px;
             -moz-border-radius: 30px;
                  border-radius: 30px;
          font-size: 14px;
          font-weight: bold;
          margin-bottom: 20px;
}



