
.index-banner {
  max-width: 1920px;
  height: 443px;
  overflow: hidden;
  position: relative;
  text-align: center;
  margin: 0 auto;
}
.index-banner .opacity-item {
    position: absolute;
    left: 0;
    top: 0;
}
.index-banner .opacity-item.active {
      z-index: 1;
}
.index-banner .banner-page {
    width: 100%;
    text-align: center;
    z-index: 2;
    bottom: 15px;
    left: 0;
    position: absolute;
}
.index-banner .banner-page > * {
      display: inline-block;
      height: 20px;
      width: 20px;
      margin: 0 5px;
      -webkit-border-radius: 50%;
         -moz-border-radius: 50%;
              border-radius: 50%;
      background: #fff;
}
.index-banner .banner-page > *.active {
        background: #041238;
}
.index-banner .fade-enter-active, .index-banner .fade-leave-active {
    -webkit-transition: opacity 1.5s;
    -o-transition: opacity 1.5s;
    -moz-transition: opacity 1.5s;
    transition: opacity 1.5s;
}
.index-banner .fade-enter, .index-banner .fade-leave-to {
    opacity: 0;
}



