
.message-enter-active, .message-leave-active {
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  -moz-transition: all 1s;
  transition: all 1s;
  overflow: hidden;
}
.message-enter,
.message-leave-to {
  height: 0 !important;
}
.user-message {
  padding: 20px 10px 20px;
}
.user-message .message-item {
    cursor: pointer;
    min-height: 60px;
    border-bottom: 1px solid #cccccc;
    margin: 15px 20px 18px;
    overflow: visible;
    position: relative;
}
.user-message .message-item:hover {
      background: #f8f8f8;
}
.user-message .message-item.active .letter-content {
      border-top: dashed 1px #ccc;
}
.user-message .message-item .letter-content {
      padding: 16px 10px;
      color: #888;
      line-height: 1.5;
      font-size: 14px;
}
.user-message .message-item .mark {
      width: 6px;
      height: 6px;
      -webkit-border-radius: 50%;
         -moz-border-radius: 50%;
              border-radius: 50%;
      display: block;
      background: #f66;
      position: absolute;
      top: 7px;
      left: -10px;
}
.user-message .message-item h2 {
      color: #606060;
      font-size: 14px;
      display: block;
      font-weight: bold;
      line-height: 19px;
}
.user-message .message-item .date {
      font-size: 12px;
      color: #999999;
      display: block;
      line-height: 16px;
      margin: 5px 0 10px;
}



