
.game-menu {
  height: 49px;
  margin: 21px 0;
}
.game-menu li {
  position: relative;
  float: left;
  border: 2px transparent solid;
  color: #3d3d3d;
  font-size: 18px;
  line-height: 45px;
  height: 45px;
}
.game-menu li a {
  line-height: 28px;
  height: 28px;
  padding: 0 24px;
  border-right: 1px solid #e4e4e4;
  display: inline-block;
  vertical-align: middle;
}
.game-menu li.active {
  border-color: #ed7e12;
  color: #ed7e12;
}
.game-menu li.active a, .game-menu li .bdnone {
  border: none;
}
.game-menu li.active:after, .game-menu li.active:before {
  content: '';
  position: absolute;
  left: 50%;
  bottom: -20px;
  margin-left: -10px;
  height: 0;
  width: 0;
  border: 10px solid transparent;
}
.game-menu li.active:after {
  border-top-color: #fff;
  bottom: -18px;
}
.game-menu li.active:before {
  border-top-color: #ed7e12;
}
.game-menu-more {
  margin-top: 21px;
  line-height: 49px;
  background: url(~images/index/more.png) no-repeat right;
  font-size: 14px;
  padding-right: 28px;
}
.game-list {
  overflow: hidden;
  margin-bottom: 60px;
}
.game-list li {
  float: left;
  width: 358px;;
  background: #f6f6f6;
  margin-right: 8px;
  height: 166px;
  margin-bottom: 3px;
}
.game-list .item-other, .game-list .item-other img {
  float: right;
  margin: 0;
  width: 294px;
  height: 335px;
  margin-right: 0;
}
.game-list li img {
  height: 166px;
  float: left;
  width: 166px;
}
.game-list li .list-item {
  padding-left: 186px;
  padding-top: 33px;
}
.game-list li .list-item h2 {
  font-size: 18px;
  font-weight: bold;
  color: #3d3d3d;
}
.game-list li .star-list {
  line-height: 33px;
}
.game-list li .star-info {
  display: inline-block;
  height: 11px;
  width: 55px;
  background: url(~images/index/xing.png) repeat-x;
}
.game-list li .tag-list span {
  font-size: 14px;
  display: inline-block;
  margin-right: 21px;
}
.game-list li .game-in {
  display: inline-block;
  width: 87px;
  height: 26px;
  margin-top: 22px;
  text-align: center;
  -webkit-border-radius: 15px;
     -moz-border-radius: 15px;
          border-radius: 15px;;
  line-height: 26px;
  color: #fff;
  background: #ed7e12;
}




