
.awards-list {
  padding: 18px 0 15px 12px;
  position: relative;
  float: left;
  width: 517px;
  background: #fff;
  height: 147px;
  -webkit-border-radius: 30px;
     -moz-border-radius: 30px;
          border-radius: 30px;
}
.awards-list li {
    white-space: nowrap;
    margin: 0 0 0 28px;
    line-height: 28px;
}
.awards-list .list-warp {
    overflow: hidden;
    height: 112px;
}
.awards-list .name {
    width: 23px;
    position: absolute;
    padding-top: 35px;
    -webkit-border-radius: 20px;
       -moz-border-radius: 20px;
            border-radius: 20px;
    text-align: center;
    height: 125px;
    top: 12px;
    left: -11px;
    background: #ed7e12;
    color: #fff;
}



