
.table-data table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}
.table-data .loadding {
  padding: 100px 0;
  text-align: center;
}
.table-data .mode1 th {
  color: #333;
  padding: 20px 16px;
  border: 1px solid #ccc;
  background: #fff;
}
.table-data .mode1 td {
  padding: 16px 10px;
  color: #333;
  font-size: 13px;
  text-align: center;
  border: 1px solid #ccc;
  background: #fff;
}
.table-data .model2 th {
  background: #dbd9d9;
  font-size: 12px;
  font-weight: normal;
  padding: 0;
  white-space: nowrap;
  border: 0;
  height: 32px;
  border-top: solid 1px #e5e5e5;
  text-align: center;
}
.table-data .model2 td {
  border: 0;
  height: 35px;
  width: auto;
  text-align: center;
  border-bottom: solid 1px #e5e5e5;
  text-align: center;
}
.table-data .model2 tr:nth-child(2n+1) {
  background: #f9f9f9;
}



