
.user-index {
  padding: 20px;
}
.user-index .user-item-box {
    -webkit-border-radius: 0;
       -moz-border-radius: 0;
            border-radius: 0;
    border: solid 1px #ccc;
    margin: 20px;
    position: relative;
    overflow: hidden;
}
.user-index .userhome-title {
    width: 150px;
    font-size: 18px;
    color: #333;
    text-align: center;
}
.user-index .userhome-title.money-title {
      line-height: 305px;
}
.user-index .right-list {
    border-left: solid 1px #ccc;
    background: #fff;
    padding-top: 15px;
    margin-left: 150px;
    overflow: hidden;
    padding: 0 20px;
}
.user-index .right-list .money-item {
      float: left;
      line-height: 61px;
      height: 61px;
      width: 50%;
      border-bottom: 1px solid #e0e0e0;
}
.user-index .right-list .money-item .amountval {
        font-size: 16px;
        float: left;
        display: inline-block;
        color: #ccc;
}
.user-index .right-list .money-item.noboder {
        border: none;
}
.user-index .right-list .money-item .iconfont {
        margin-left: 6px;
        font-size: 20px;
        cursor: pointer;
        font-weight: 100;
        color: #ccc;
}
.user-index .right-list .label {
      color: #666666;
      font-size: 18px;
      float: left;
      margin-right: 25px;
}
.user-index .functionmenu .userhome-title {
    line-height: 200px;
}
.user-index .functionmenu .right-list {
    text-align: center;
    padding: 20px 0;
}
.user-index .functionmenu .day-funs {
    display: inline-block;
    text-align: left;
    width: 178px;
    height: 60px;
    margin: 10px;
    -webkit-border-radius: 4px;
       -moz-border-radius: 4px;
            border-radius: 4px;
    background: #ccc;
    line-height: 60px;
    padding-left: 70px;
    -webkit-box-sizing: border-box;
       -moz-box-sizing: border-box;
            box-sizing: border-box;
    font-size: 16px;
    color: #FFF;
    background-position: 25px center;
    background-repeat: no-repeat;
    -moz-background-size: 35px;
         background-size: 35px;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    -moz-transition: all 0.5s;
    transition: all 0.5s;
}
.user-index .functionmenu .day-funs.u-ico1 {
      background-image: url(~images/icons/u_ico1.png);
      background-color: #2cb187;
}
.user-index .functionmenu .day-funs.u-ico2 {
      background-image: url(~images/icons/u_ico2.png);
      background-color: #cf4eaf;
}
.user-index .functionmenu .day-funs.u-ico3 {
      background-image: url(~images/icons/u_ico3.png);
      background-color: #eb4646;
}
.user-index .functionmenu .day-funs.u-ico4 {
      background-image: url(~images/icons/u_ico4.png);
      background-color: #3ebc12;
}
.user-index .functionmenu .day-funs.u-ico5 {
      background-image: url(~images/icons/u_ico5.png);
      background-color: #00a0ea;
}
.user-index .functionmenu .day-funs.u-ico6 {
      background-image: url(~images/icons/u_ico6.png);
      background-color: #4159e9;
}
.user-index .functionmenu .day-funs:hover {
      -webkit-transform: scale(1.1);
         -moz-transform: scale(1.1);
          -ms-transform: scale(1.1);
           -o-transform: scale(1.1);
              transform: scale(1.1);
}



