
.about-us {
  padding-bottom: 75px;
}
.about-us .content-warp {
    width: 1140px;
    margin: 0 auto;
    background: #fff;
    -webkit-box-shadow: 0px 2px 4px #dde2e7;
       -moz-box-shadow: 0px 2px 4px #dde2e7;
            box-shadow: 0px 2px 4px #dde2e7;
    padding-bottom: 38px;
}
.about-us .tab-menu {
    height: 116px;
    text-align: center;
}
.about-us .tab-menu .tab-menu-item {
      display: inline-block;
      width: 80px;
      height: 80px;
      padding-top: 45px;
      border: 1px solid #cfcfcf;
      -webkit-border-radius: 40px;
         -moz-border-radius: 40px;
              border-radius: 40px;
      margin: 15px 20px 0;
      -webkit-box-sizing: border-box;
         -moz-box-sizing: border-box;
              box-sizing: border-box;
      font-size: 14px;
      color: #333333;
      cursor: pointer;
      position: relative;
}
.about-us .tab-menu .tab-menu-item.active, .about-us .tab-menu .tab-menu-item:hover {
        border: 0;
        background: -prefix-linear-zgradient(top, #ffb600, #ed7f12);
        background: -webkit-gradient(linear, left top, left bottom, from(#ffb600), to(#ed7f12));
        background: -webkit-linear-gradient(top, #ffb600, #ed7f12);
        background: -moz-linear-gradient(top, #ffb600, #ed7f12);
        background: -o-linear-gradient(top, #ffb600, #ed7f12);
        background: linear-gradient(to bottom, #ffb600, #ed7f12);
        color: #fff;
}
.about-us .tab-menu .tab-menu-item.active i, .about-us .tab-menu .tab-menu-item:hover i {
          background-position: center -24px !important;
}
.about-us .tab-menu .tab-menu-item.active i.i6, .about-us .tab-menu .tab-menu-item:hover i.i6 {
          background-position: center -25px !important;
}
.about-us .tab-menu .tab-menu-item i {
        position: absolute;
        width: 24px;
        height: 24px;
        left: 28px;
        top: 13px;
}
.about-us .tab-menu .tab-menu-item i.i1 {
          background: url(~images/about/a1.png) no-repeat center top;
}
.about-us .tab-menu .tab-menu-item i.i2 {
          background: url(~images/about/a2.png) no-repeat center top;
}
.about-us .tab-menu .tab-menu-item i.i3 {
          background: url(~images/about/a3.png) no-repeat center top;
}
.about-us .tab-menu .tab-menu-item i.i4 {
          background: url(~images/about/a4.png) no-repeat center top;
}
.about-us .tab-menu .tab-menu-item i.i5 {
          background: url(~images/about/a5.png) no-repeat center top;
          height: 25px;
}
.about-us .tab-menu .tab-menu-item i.i6 {
          background: url(~images/about/a6.png) no-repeat center top;
}
.about-us .tab-menu .tab-menu-item i.i7 {
          background: url(~images/about/a7.png) no-repeat center top;
}
.about-us .content-warp .content-info {
    margin: 0 18px;
    background: #f0f0f0;
    padding: 44px;
    line-height: 1.4;
    font-size: 14px;
}
.about-us .content-warp .content-info h3 {
      margin: 20px 0;
      line-height: 1;
      font-size: 16px;
      color: #ed7e12;
      position: relative;
      padding-left: 10px;
}
.about-us .content-warp .content-info h3::before {
        display: block;
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        height: 15px;
        border-left: 2px solid #ed7e12;
}
.about-us .content-warp .content-info p, .about-us .content-warp .content-info ol {
      font-size: 14px;
      overflow: hidden;
      line-height: 30px;
      color: #333333;
      text-indent: 2em;
      margin-bottom: 20px;
}



