
.message-enter-active, .message-leave-active {
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  -moz-transition: all 1s;
  transition: all 1s;
  overflow: hidden;
}
.message-enter,
.message-leave-to {
  height: 0 !important;
}
.notice-banner {
  background: url("~images/banner/notice-banner.jpg") center center no-repeat;
  height: 260px;
}
.show-notice {
  padding-top: 20px;
  background: #fff;
  padding: 20px 22px 30px;
  margin-bottom: 74px;
  min-height: 800px;
}
.notice-left {
  width: 230px;
  float: left;
  text-align: center;
}
.notice-left h2 {
    font-size: 18px;
    line-height: 30px;
    background: #cbcbcb;
}
.notice-left .notice-contact-us {
    background: #f6f6f6;
    height: 157px;
    overflow: hidden;
    line-height: 41px;
    font-size: 16px;
    color: #333;
}
.notice-left .online-text {
    height: 34px;
    width: 87px;
    line-height: 34px;
    -webkit-border-radius: 17px;
       -moz-border-radius: 17px;
            border-radius: 17px;
    background: #ed7e12;
    display: inline-block;
    color: #fff;
    margin-top: 19px;
}
.notice-left .code {
    margin: 63px  0 16px 0;
    font-size: 16px;
}
.notice-list {
  padding: 0 20px 20px;
  background: #f6f6f6;
  width: 790px;
  float: right;
}
.notice-list .list-item {
    border-bottom: 1px solid #fff;
    cursor: pointer;
}
.notice-list .list-title {
    padding-top: 24px;
    padding-bottom: 11px;
}
.notice-list .list-title h3 {
      font-size: 18px;
      white-space: nowrap;
      font-weight: bold;
}
.notice-list .list-title h3 .list-time {
        float: right;
        font-weight: lighter;
        color: #999;
}
.notice-list .item-info {
    margin-top: 3px;
    background: #fff;
    padding: 40px 20px;
    line-height: 25px;
    font-size: 14px;
}



