
.alert-tips {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 200;
  text-align: center;
}
.alert-tips .alert-tips-content {
    background: url("./bg.png");
    width: 836px;
    height: 628px;
    display: inline-block;
    margin-top: 10%;
    -webkit-border-radius: 15px;
       -moz-border-radius: 15px;
            border-radius: 15px;
    -moz-background-size: 100% 100%;
         background-size: 100% 100%;
    color: #000;
    padding: 88px 264px 120px 30px;
    position: relative;
}
.alert-tips .alert-tips-content .iclose {
      position: absolute;
      right: 76px;
      top: 18px;
      font-size: 50px;
      color: #fff;
      cursor: pointer;
      background-color: #0e1a95;
      -webkit-border-radius: 50%;
         -moz-border-radius: 50%;
              border-radius: 50%;
}
.alert-tips .alert-tips-content .content-info {
      height: 425px;
      overflow-y: auto;
}
.alert-tips .alert-tips-content .content-info .title {
        text-align: center;
        font-size: 24px;
        color: #333333;
        white-space: nowrap;
        padding-left: 180px;
        padding-right: 46px;
        line-height: 46px;
}
.alert-tips .alert-tips-content .content-info .conent {
        font-size: 18px;
        color: #333333;
        line-height: 28px;
        overflow-x: hidden;
        text-align: left;
        padding: 0 0 5px 0;
}
.alert-tips .alert-tips-content .de-code {
      position: absolute;
      bottom: 120px;
      left: 30px;
      width: 521px;
      height: 140px;
      text-align: left;
}
.alert-tips .alert-tips-content .de-code span {
        color: #f00;
        font-size: 18px;
}



