
.header {
  height: 55px;
  background: #010016;
  color: #a4a3a7;
  font-size: 14px;
  line-height: 53px;
}
.header .xuanzhuan {
    -webkit-animation: rotate11 2s linear infinite;
       -moz-animation: rotate11 2s linear infinite;
         -o-animation: rotate11 2s linear infinite;
            animation: rotate11 2s linear infinite;
    display: inline-block;
}
.header .fl > a {
    margin: 0 15px;
}
.header .islogin-info {
    color: #09edff;
}
.header .login-form {
    line-height: 27px;
    display: inline-block;
}
.header .login-form p {
    border: 1px solid #293758;
    padding-left: 60px;
    padding-right: 5px;
    display: inline-block;
    margin-right: 9px;
    vertical-align: middle;
    position: relative;
    height: 25px;
    width: 135px;
}
.header .login-form p input {
    color: #a4a3a7;
    width: 100%;
    background: none;
    height: 25px;
    outline: none;
    vertical-align: top;
}
.header .login-form p label {
    position: absolute;
    left: 9px;
    top: 0px;
    line-height: 25px;
}
.header .login-form p.identifying-code {
    width: 145px;
    padding-left: 85px;
}
.header .login-form .btn {
    display: inline-block;
    width: 68px;
    height: 24px;
    line-height: 22px;
    border: 1px solid #ff4242;
    background: #d90000;
    color: #fff;
    -webkit-box-sizing: border-box;
       -moz-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    font-size: 12px;
    vertical-align: middle;
}
.header .login-form .btn-register {
    background: #ffd300;
    border-color: #ffcb5a;
    margin-left: 6px;
    color: #121212;
}
.header .free-try-play {
    display: inline-block;
    margin: 0 17px;
    vertical-align: middle;
}
.header .cl-yelow {
    color: #ffd924 !important;
}
.header .cl-red {
    color: #ff1e1e !important;
}
.header .btn-other {
    border: solid 1px #fff;
    color: #fff;
    display: inline-block;
    position: relative;
    display: inline;
    padding: 2px 24px;
    font-size: 14px;
    margin-left: 10px;
    text-align: center;
    -webkit-border-radius: 3px;
       -moz-border-radius: 3px;
            border-radius: 3px;
    font-weight: 500;
}
.header .server {
    margin: 0;
    -webkit-animation: flashs 0.6s infinite;
       -moz-animation: flashs 0.6s infinite;
         -o-animation: flashs 0.6s infinite;
            animation: flashs 0.6s infinite;
}
@-webkit-keyframes flashs {
0% {
    color: #f00;
}
100% {
    color: yellow;
}
}
@-moz-keyframes flashs {
0% {
    color: #f00;
}
100% {
    color: yellow;
}
}
@-o-keyframes flashs {
0% {
    color: #f00;
}
100% {
    color: yellow;
}
}
@keyframes flashs {
0% {
    color: #f00;
}
100% {
    color: yellow;
}
}
.header .header-fast-btn {
    display: inline-block;
    margin: 0 6px;
}



