
.right-server {
  position: fixed;
  z-index: 100;
  right: 10px;
  top: 50%;
  margin-top: -257px;
  width: 141px;
  height: 315px;
  padding: 166px 17px 0;
  background: url(./images/bg.png) no-repeat;
}
.right-server .close {
    position: absolute;
    top: -35px;
    left: 50%;
    margin-left: -18px;
    cursor: pointer;
    color: #c70d22;
}
.right-server .close .iconfont {
      font-size: 36px;
}
.right-server .cont-item {
    display: block;
    text-align: center;
    background: #e1192f;
    line-height: 29px;
    color: #fff;
    font-size: 14px;
    margin-top: 1px;
}
.right-server .cont-item img {
      margin: 0 5px;
}
.right-server .cont-qq {
    margin-top: 32px;
    font-size: 16px;
    font-weight: bold;
}
.right-server .cont-qq img {
      margin-top: -20px;
      margin-right: 6px;
}
.right-server .cont-qq .cont-qq-num {
      text-align: center;
      background-color: #ed7e12;
      -webkit-border-radius: 15px;
         -moz-border-radius: 15px;
              border-radius: 15px;
      line-height: 30px;
      color: #fff;
      margin-top: 5px;
}
.right-server .cont-wechat {
    margin-top: 10px;
    font-size: 16px;
    font-weight: bold;
    line-height: 38px;
}
.right-server .cont-wechat a img {
      margin-right: 5px;
}
.right-server .cont-wechat img {
      vertical-align: top;
}
.right-server .down-client {
    line-height: 30px;
    font-size: 14px;
    color: #fff;
    background-color: #ed7e12;
    text-align: center;
    display: block;
    margin-top: 10px;
    -webkit-border-radius: 15px;
       -moz-border-radius: 15px;
            border-radius: 15px;
}



