
div.nav {
  height: 82px;
  border-bottom: 1PX solid #041238;
  position: relative;
  border-top: 1px solid #021646;
  background: #02091c;
  padding: 32px 0 0 0;
}
div.nav .content-warp {
    padding-left: 142px;
    -webkit-box-sizing: border-box;
       -moz-box-sizing: border-box;
            box-sizing: border-box;
}
div.nav .logo {
    width: 120px;
    left: 0;
    vertical-align: top;
    position: absolute;
    top: -32px;
    z-index: 110;
}
div.nav li {
    float: left;
    color: #fff;
    position: relative;
    text-align: center;
    border-left: 1px solid #1c2233;
}
div.nav li:hover div {
      display: block !important;
}
div.nav li.none-border {
    border-left: none;
}
div.nav li.active:after, div.nav nav li:hover:after {
    content: "";
    z-index: 100;
    position: absolute;
    height: 2px;
    background: #ffed24;
    width: 80%;
    bottom: -14px;
    left: 50%;
    margin-left: -40%;
}
div.nav li > a {
    display: block;
    font-size: 14px;
    margin: 0 25px;
}
div.nav li small {
    display: block;
    color: #909090;
    font-size: 12px;
    margin-top: 11px;
}
div.nav li > img {
    -webkit-animation: topbtm 0.5s linear infinite alternate;
       -moz-animation: topbtm 0.5s linear infinite alternate;
         -o-animation: topbtm 0.5s linear infinite alternate;
            animation: topbtm 0.5s linear infinite alternate;
    position: absolute;
}
@-webkit-keyframes topbtm {
from {
    top: -23px;
}
to {
    top: -19px;
}
}
@-moz-keyframes topbtm {
from {
    top: -23px;
}
to {
    top: -19px;
}
}
@-o-keyframes topbtm {
from {
    top: -23px;
}
to {
    top: -19px;
}
}
@keyframes topbtm {
from {
    top: -23px;
}
to {
    top: -19px;
}
}
div.nav .top-subnav-wp {
    display: none;
    text-align: center;
    position: absolute;
    top: 78px;
    left: 0;
    right: 0;
    width: 100%;
    height: 202px;
    background-color: rgba(0, 13, 49, 0.8);
    z-index: 99;
    -webkit-transition: all .15s ease-out;
    -o-transition: all .15s ease-out;
    -moz-transition: all .15s ease-out;
    transition: all .15s ease-out;
}
div.nav .top-subnav-wp .container {
      width: 1100px;
      margin: 0 auto;
      position: relative;
}
div.nav .top-subnav-wp .cell-5 {
      float: left;
}
div.nav .top-subnav-wp a:hover .sub-icon-ag {
      -webkit-transform: rotateY(180deg);
         -moz-transform: rotateY(180deg);
              transform: rotateY(180deg);
}
div.nav .top-subnav-wp a:hover .sub-icon {
      -webkit-transform: rotateY(180deg);
         -moz-transform: rotateY(180deg);
              transform: rotateY(180deg);
}
div.nav .top-subnav-wp .sp-nav-mg {
      background-position: 0 -315px;
}
div.nav .top-subnav-wp .sp-nav-dt {
      background-position: 0 -143px;
}
div.nav .top-subnav-wp .sp-nav-nt {
      background-position: 0 -826px;
}
div.nav .top-subnav-wp .sp-nav-pt {
      background-position: 0 18px;
}
div.nav .top-subnav-wp .sp-nav-png {
      background-position: 0 -500px;
}
div.nav .top-subnav-wp .sp-nav-qt {
      background-position: 0 -663px;
}
div.nav .top-subnav-wp i.sp-nav-ag1 {
      background: url(./images/subnav-ag.png) no-repeat;
      background-position: -22px -170px;
      width: 155px;
}
div.nav .top-subnav-wp .zr-box > a {
      margin: 0;
      padding: 19px 0;
}
div.nav .top-subnav-wp .buyu-box > a {
      margin: 0 100px;
}
div.nav .top-subnav-wp .sub-icon {
      background-image: url(./images/subnav-slot.png?v=1);
      background-repeat: no-repeat;
      display: block;
      -webkit-transition: .6s all linear;
      -o-transition: .6s all linear;
      -moz-transition: .6s all linear;
      transition: .6s all linear;
      width: 144px;
      height: 140px;
}
div.nav .top-subnav-wp a {
      display: inline-block;
      text-align: center;
      position: relative;
      padding: 19px 22px;
}
div.nav .top-subnav-wp .m-row > .cell-5 {
      width: 155px;
      padding: 12px 0 !important;
}
div.nav .top-subnav-wp .top-subnav-bd:before {
      content: '';
      height: 100%;
      vertical-align: middle;
      display: inline-block;
}
div.nav .top-subnav-wp .top-subnav-bd {
      display: block;
      height: 140px;
      text-align: center;
      position: relative;
}
div.nav .top-subnav-wp .top-subnav-bd .icon-wp {
      display: inline-block;
      vertical-align: middle;
}
div.nav .top-subnav-wp .top-subnav-ft {
      color: #fff;
      padding: 5px;
}
div.nav .top-subnav-wp .btn {
      background-color: #09affe;
      padding: 0 20px;
      height: 22px;
      line-height: 22px;
      -webkit-border-radius: 10px;
         -moz-border-radius: 10px;
              border-radius: 10px;
      font-size: 12px;
      display: inline-block;
      color: #fff;
      text-align: center;
      cursor: pointer;
}
div.nav .top-subnav-wp .btn:hover {
      background-color: #f3d759;
      -webkit-transition: .3s;
      -o-transition: .3s;
      -moz-transition: .3s;
      transition: .3s;
      color: #4f2f12;
}
div.nav .top-subnav-wp .top-subnav .app {
      width: 372px;
      height: 256px;
}
div.nav .top-subnav-wp i.sp-nav-ebet {
      background: url(./images/zr02.png) no-repeat;
      background-position: center;
}
div.nav .top-subnav-wp .sub-icon-ag {
      background: url(./images/zr04.png) no-repeat;
      background-position: center;
      display: block;
      -webkit-transition: .6s all linear;
      -o-transition: .6s all linear;
      -moz-transition: .6s all linear;
      transition: .6s all linear;
      width: 170px;
      height: 150px;
}



