
.help-gold .moneywrap {
  margin: 0 auto 20px;
  text-align: center;
  color: #1e327e;
  cursor: pointer;
}
.help-gold .moneywrap .moneybox {
    width: 180px;
    height: 67px;
    padding-top: 10px;
    -webkit-border-radius: 5px;
       -moz-border-radius: 5px;
            border-radius: 5px;
    border: 1px solid #cfcfcf;
    float: none !important;
    background: #fff;
    display: inline-block;
    margin: 0 5px;
    margin-bottom: 10px;
}
.help-gold .moneywrap .moneybox:hover, .help-gold .moneywrap .moneybox.active {
      border: 1px solid #1e327e;
      background: #1e327e;
      color: #fff;
}
.help-gold .moneywrap .moneybox h2 {
      font-weight: normal;
      font-size: 18px;
      margin: 0 auto 5px;
      text-align: center;
}
.help-gold .moneywrap .moneybox .date {
      width: 100%;
      text-align: center;
      display: block;
      font-size: 12px;
}



