
.addCard {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 99;
  top: 0;
  left: 0;
}
.addCard > div {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -200px;
    margin-left: -200px;
    background-color: #fff;
    -webkit-border-radius: 10px;
       -moz-border-radius: 10px;
            border-radius: 10px;
}
.addCard .inputbox {
    width: 550px;
    min-height: 45px;
    margin: 0 auto 12px;
    clear: both;
    position: relative;
}
.addCard .inputbox .label {
      font-size: 16px;
      color: #666666;
      float: left;
      height: 45px;
      line-height: 45px;
      min-width: 112px;
      text-align: right;
}
.addCard .inputbox input {
      width: 420px;
      height: 40px;
      float: left;
      -webkit-border-radius: 4px;
         -moz-border-radius: 4px;
              border-radius: 4px;
      outline: none;
      background: #f8f8f8;
      border: solid 1px #cacaca;
      font-size: 14px;
      text-indent: 15px;
      color: #666;
}
.addCard h1 {
    font-size: 24px;
    color: #282828;
    margin-bottom: 15px;
    margin-top: 15px;
    text-align: center;
    line-height: 31px;
    font-weight: normal;
}
.addCard .formbtn {
    margin: 10px auto 20px;
    display: block;
    width: 100px;
    cursor: pointer;
    letter-spacing: 4px;
    height: 40px;
    line-height: 40px;
    font-size: 16px;
    background: #13a1ca;
    -webkit-border-radius: 4px;
       -moz-border-radius: 4px;
            border-radius: 4px;
    color: #fff;
    text-align: center;
    font-weight: bold;
}
.addCard .cardTips {
    line-height: 30px;
    text-align: center;
    margin-bottom: 10px;
}
.addCard .closeBtn {
    display: block;
    position: absolute;
    top: 10px;
    right: 16px;
    cursor: pointer;
    color: #ccc;
    z-index: 1;
}
.addCard .closeBtn .iconfont {
      font-size: 30px;
}



