
.modal-dialog {
  position: relative;
  width: 700px;
  margin: 180px auto;
  padding-bottom: 20px;
  background: #fff;
  -webkit-border-radius: 8px;
     -moz-border-radius: 8px;
          border-radius: 8px;
  -webkit-transition: -webkit-transform 0.15s ease-out;
  transition: -webkit-transform 0.15s ease-out;
  -o-transition: -o-transform 0.15s ease-out;
  -moz-transition: transform 0.15s ease-out, -moz-transform 0.15s ease-out;
  transition: transform 0.15s ease-out;
  transition: transform 0.15s ease-out, -webkit-transform 0.15s ease-out, -moz-transform 0.15s ease-out, -o-transform 0.15s ease-out;
}
.modal-dialog .close {
    position: absolute;
    z-index: 1;
    top: 10px;
    right: 10px;
    cursor: pointer;
    color: #ccc;
}
.forget-password .pop-content {
  position: relative;
  padding-bottom: 30px;
  padding-top: 54px;
}
.forget-password .pop-content h2 {
    font-size: 24px;
    color: #282828;
    margin-bottom: 15px;
    text-align: center;
    font-weight: normal;
}
.forget-password .form-box {
  width: 300px;
  margin: 0 auto;
  margin-top: 20px;
}
.forget-password .form-box .form-group {
    height: 34px;
    line-height: 34px;
    position: relative;
    margin-bottom: 14px;
    width: 100%;
}
.forget-password .form-box p {
    text-align: center;
    color: #999;
}
.forget-password .form-box .captcha-code {
    width: 90px;
    height: 34px;
    float: left;
    display: block;
    position: relative;
    background: no-repeat;
}
.forget-password .form-box .captcha-code img {
      height: 34px;
      width: 90px;
      vertical-align: top;
}
.forget-password .form-box .ps-input {
    width: 300px;
    height: 34px;
    border: 0;
    background: 0 0;
    border-bottom: 1px solid #ccc;
    outline: 0;
    line-height: 34px;
    font-size: 16px;
    color: #333;
    padding: 0 20px 0 30px;
}
.forget-password .form-box .ps-input2 {
    width: 210px;
    float: left;
    height: 34px;
    border-bottom: 1px solid #ccc;
    outline: 0;
    line-height: 34px;
    font-size: 16px;
    color: #333;
    padding: 0 20px 0 30px;
}
.forget-password .form-box i.iconfont {
    position: absolute;
    height: 100%;
    line-height: 34px;
    top: 0;
    left: 0;
    font-size: 24px;
    display: inline-block;
}
.forget-password .icon_popup {
  position: absolute;
  display: block;
}
.forget-password .submit {
  width: 300px;
  height: 40px;
  background: #13a1ca;
  text-align: center;
  line-height: 40px;
  cursor: pointer;
  font-size: 16px;
  color: #fff;
  text-align: center;
  display: block;
  -webkit-border-radius: 4px;
     -moz-border-radius: 4px;
          border-radius: 4px;
}
.forget-password .pop-tab {
  overflow: hidden;
  text-align: center;
  width: 350px;
  margin: 0 auto;
}
.forget-password .pop-tab li {
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    color: #666;
    padding: 0 6px;
    line-height: 32px;
    display: inline-block;
    height: 32px;
    margin: 0 15px;
}
.forget-password .pop-tab li.active, .forget-password .pop-tab li:hover {
      color: #13a1ca;
      border-bottom: 2px solid #13a1ca;
}



