
.updata-info {
  margin: 20px;
  border: solid 1px #ccc;
  padding-bottom: 10px;
}
.updata-info p, .updata-info div.lines {
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 16px;
    color: #333;
    padding-left: 45px;
    font-weight: 400;
}
.updata-info .red {
    color: #f00;
}
.updata-info .btn-submit {
    float: none !important;
    display: inline-block;
    width: 82px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    font-size: 14px;
    color: #fff;
    background: #1e327e;
    -webkit-border-radius: 4px;
       -moz-border-radius: 4px;
            border-radius: 4px;
    margin-left: 11px;
}
.updata-info input {
    padding: 6px;
    border: 1px #ccc solid;
}



