
.error-main {
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100%;
  background: #fff;
  height: 100%;
  overflow: auto;
}
.error-main .err-header .wrap {
    height: 96px;
    background-color: #151b28;
}
.error-main .err-content {
    width: 1200px;
    margin: 0 auto;
    overflow: hidden;
}
.error-main .err-header .wrap .logo-group {
    background: url("~images/logo.png") no-repeat;
    -moz-background-size: contain;
         background-size: contain;
    width: 235px;
    height: 80px;
    margin-top: 8px;
    float: left;
}
.error-main .err-header .logo {
    display: block;
    width: 100%;
    height: 100%;
}
.error-404 {
  position: relative;
  z-index: 9999;
  width: 1200px;
  margin: 0 auto;
  padding-top: 100px;
  padding-bottom: 80px;
}
.error-404 .fbbg {
    width: 750px;
    height: 350px;
    margin: 0 auto;
}
.error-404 .bgmaintain {
    -moz-background-size: contain;
         background-size: contain;
    background-position: center center;
}
.error-404 .title {
    font-size: 48px;
    font-weight: bold;
    color: #333;
    padding-left: 300px;
    letter-spacing: 10px;
    margin-bottom: 10px;
}
.error-404 .des {
    color: #333;
    padding-left: 300px;
    font-size: 16px;
    letter-spacing: 4px;
    line-height: 30px;
}
.error-404 .btn {
    padding-left: 300px;
    margin-top: 20px;
}
.error-404 .btn a {
    display: inline-block;
    padding: 10px 40px;
    color: #fff;
    -webkit-border-radius: 4px;
       -moz-border-radius: 4px;
            border-radius: 4px;
    font-size: 16px;
    margin: 0px 15px;
}
.error-404 .btn .r_btn {
    background: #d0352b;
}
.error-404 .btn .b_btn {
    background: #4a9fc6;
}



