
.ajax-load-animate {
  /*background: rgba(0,0,0,0.5);*/
  position: fixed; z-index:9999998; top:0px; left: 0; width: 100%; height: 100%
}
.animate-item-warp { position: relative;
  height: 150px;
  width: 150px;
  line-height: 150px;
  text-align: center;
  font-size: 20px;
  color: yellow;
  top: 50%; left: 50%;
  -webkit-transform: translate(-50%, -50%);
     -moz-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
       -o-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.animate-item-one:after, .animate-item-one:before,
.animate-item-two:after, .animate-item-two:before {
  content: '';
  top: 0;
  bottom: 0;
  display: block;
  position: absolute;
  background-color: rgba(0, 0, 0, 0);
  border: 5px solid rgba(0, 183, 229, 0.9);
  opacity: .9;
  border-left: 5px solid rgba(0, 0, 0, 0);
  -webkit-border-radius: 50%;
     -moz-border-radius: 50%;
          border-radius: 50%;
  width: 100%;
  height: 100%;
  -webkit-box-shadow: 0 0 30px #2187e7;
     -moz-box-shadow: 0 0 30px #2187e7;
          box-shadow: 0 0 30px #2187e7;
}
.animate-item-one:before {
  border-top: 5px solid rgba(0, 0, 0, 0);
  width: 80%;
  height: 80%;
  left: 10%;
  top: 10%;
  -webkit-animation: spinoff 2s infinite linear;
     -moz-animation: spinoff 2s infinite linear;
       -o-animation: spinoff 2s infinite linear;
          animation: spinoff 2s infinite linear;
}
.animate-item-one:after {
  border-top: 5px solid rgba(0, 0, 0, 0);
  -webkit-animation: spin 2s infinite linear;
     -moz-animation: spin 2s infinite linear;
       -o-animation: spin 2s infinite linear;
          animation: spin 2s infinite linear;
}
@-webkit-keyframes spinoff {
0% { -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
100% { -webkit-transform: rotate(-360deg); transform: rotate(-360deg);
}
}
@-moz-keyframes spinoff {
0% { -moz-transform: rotate(0deg); transform: rotate(0deg);
}
100% { -moz-transform: rotate(-360deg); transform: rotate(-360deg);
}
}
@-o-keyframes spinoff {
0% { -o-transform: rotate(0deg); transform: rotate(0deg);
}
100% { -o-transform: rotate(-360deg); transform: rotate(-360deg);
}
}
@keyframes spinoff {
0% { -webkit-transform: rotate(0deg); -moz-transform: rotate(0deg); -o-transform: rotate(0deg); transform: rotate(0deg);
}
100% { -webkit-transform: rotate(-360deg); -moz-transform: rotate(-360deg); -o-transform: rotate(-360deg); transform: rotate(-360deg);
}
}
@-webkit-keyframes spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-moz-keyframes spin {
0% {
    -moz-transform: rotate(0deg);
         transform: rotate(0deg);
}
100% {
    -moz-transform: rotate(360deg);
         transform: rotate(360deg);
}
}
@-o-keyframes spin {
0% {
    -o-transform: rotate(0deg);
       transform: rotate(0deg);
}
100% {
    -o-transform: rotate(360deg);
       transform: rotate(360deg);
}
}
@keyframes spin {
0% {
    -webkit-transform: rotate(0deg);
       -moz-transform: rotate(0deg);
         -o-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
       -moz-transform: rotate(360deg);
         -o-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.animate-item-two:after {
  width: 80%;
  height: 80%;
  left: 10%;
  top: 10%;
  border-right: 5px solid rgba(0, 0, 0, 0);
  -webkit-animation: spin 2s infinite linear;
     -moz-animation: spin 2s infinite linear;
       -o-animation: spin 2s infinite linear;
          animation: spin 2s infinite linear;
}
.animate-item-two:before {
  border-right: 5px solid rgba(0, 0, 0, 0);
  -webkit-animation: spinPulse 2s infinite  ease-in-out;
     -moz-animation: spinPulse 2s infinite  ease-in-out;
       -o-animation: spinPulse 2s infinite  ease-in-out;
          animation: spinPulse 2s infinite  ease-in-out;
}
@-webkit-keyframes spinPulse {
0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); opacity:1; -webkit-box-shadow: 0 0 1px #2187e7; box-shadow: 0 0 1px #2187e7;
}
50% { -webkit-transform: rotate(145deg); transform: rotate(145deg); opacity:1;
}
100% { -webkit-transform: rotate(-360deg); transform: rotate(-360deg); opacity:1;
}
}
@-moz-keyframes spinPulse {
0% { -moz-transform: rotate(0deg); transform: rotate(0deg); opacity:1; -moz-box-shadow: 0 0 1px #2187e7; box-shadow: 0 0 1px #2187e7;
}
50% { -moz-transform: rotate(145deg); transform: rotate(145deg); opacity:1;
}
100% { -moz-transform: rotate(-360deg); transform: rotate(-360deg); opacity:1;
}
}
@-o-keyframes spinPulse {
0% { -o-transform: rotate(0deg); transform: rotate(0deg); opacity:1; box-shadow: 0 0 1px #2187e7;
}
50% { -o-transform: rotate(145deg); transform: rotate(145deg); opacity:1;
}
100% { -o-transform: rotate(-360deg); transform: rotate(-360deg); opacity:1;
}
}
@keyframes spinPulse {
0% { -webkit-transform: rotate(0deg); -moz-transform: rotate(0deg); -o-transform: rotate(0deg); transform: rotate(0deg); opacity:1; -webkit-box-shadow: 0 0 1px #2187e7; -moz-box-shadow: 0 0 1px #2187e7; box-shadow: 0 0 1px #2187e7;
}
50% { -webkit-transform: rotate(145deg); -moz-transform: rotate(145deg); -o-transform: rotate(145deg); transform: rotate(145deg); opacity:1;
}
100% { -webkit-transform: rotate(-360deg); -moz-transform: rotate(-360deg); -o-transform: rotate(-360deg); transform: rotate(-360deg); opacity:1;
}
}



