
.modal-dialog .close {
  position: absolute;
  z-index: 1;
  top: 10px;
  right: 10px;
  cursor: pointer;
  color: #ccc;
}
.popup .pop-content {
  position: relative;
  padding-bottom: 30px;
  padding-top: 54px;
}
.popup .pop-content h2 {
    font-size: 24px;
    color: #282828;
    margin-bottom: 15px;
    text-align: center;
    font-weight: normal;
}
.popup .form-box {
  width: 300px;
  margin: 0 auto;
}
.popup .form-box .form-group {
    height: 34px;
    line-height: 34px;
    position: relative;
    margin-bottom: 14px;
    width: 100%;
}
.popup .form-box .validate {
    display: block;
    margin: 10px 0;
    font-size: 12px;
    color: #13a1ca;
    min-height: 16px;
    text-align: center;
}
.popup .form-box p {
    text-align: center;
    color: #999;
}
.popup .form-box .captcha-code {
    width: 90px;
    height: 34px;
    float: left;
    display: block;
    position: relative;
    background: no-repeat;
}
.popup .form-box .captcha-code img {
      height: 34px;
      width: 90px;
      vertical-align: top;
}
.popup .form-box .ps-input {
    width: 300px;
    height: 34px;
    border: 0;
    background: 0 0;
    border-bottom: 1px solid #ccc;
    outline: 0;
    line-height: 34px;
    font-size: 16px;
    color: #333;
    padding: 0 20px 0 30px;
}
.popup .form-box .ps-input2 {
    width: 210px;
    float: left;
    height: 34px;
    border-bottom: 1px solid #ccc;
    outline: 0;
    line-height: 34px;
    font-size: 16px;
    color: #333;
    padding: 0 20px 0 30px;
}
.popup .form-box i.iconfont {
    position: absolute;
    height: 100%;
    line-height: 34px;
    top: 0;
    left: 0;
    font-size: 24px;
    display: inline-block;
}
.popup .form-box span.iconfont {
    position: absolute;
    top: 0;
    right: 10px;
    font-size: 24px;
    cursor: pointer;
    line-height: 34px;
    height: 100% !important;
    color: #f66;
}
.popup .icon_popup {
  position: absolute;
  display: block;
}
.popup .submit {
  width: 300px;
  height: 40px;
  background: #13a1ca;
  text-align: center;
  line-height: 40px;
  cursor: pointer;
  font-size: 16px;
  color: #fff;
  text-align: center;
  display: block;
  -webkit-border-radius: 4px;
     -moz-border-radius: 4px;
          border-radius: 4px;
}



