
.wrapcards {
  overflow: hidden;
  width: 100%;
  margin: 0 auto 40px;
  padding-bottom: 40px;
}
.wrapcards .box {
    margin: 0 23px 20px;
    height: 188px;
    width: 320px;
    display: inline-block;
    vertical-align: top;
    -moz-background-size: 100% 100%;
         background-size: 100% 100%;
    background-repeat: no-repeat;
    -moz-background-size: contain;
         background-size: contain;
}
.wrapcards .box.bg1 {
      background-image: url("./images/01.jpg");
}
.wrapcards .box.bg2 {
      background-image: url("./images/02.jpg");
}
.wrapcards .box.bg3 {
      background-image: url("./images/03.jpg");
}
.wrapcards .box.bg4 {
      background-image: url("./images/04.jpg");
}
.wrapcards .box.bg5 {
      background-image: url("./images/05.jpg");
}
.wrapcards .box.bg6 {
      background-image: url("./images/06.jpg");
}
.wrapcards .box.bg7 {
      background-image: url("./images/07.jpg");
}
.wrapcards .box.bg8 {
      background-image: url("./images/08.jpg");
}
.wrapcards .box.bg9 {
      background-image: url("./images/09.jpg");
}
.wrapcards .box.bg10 {
      background-image: url("./images/10.jpg");
}
.wrapcards .box.bg11 {
      background-image: url("./images/11.jpg");
}
.wrapcards .box.bg12 {
      background-image: url("./images/12.jpg");
}
.wrapcards .box.bg13 {
      background-image: url("./images/13.jpg");
}
.wrapcards .box.add {
      border: dashed 2px #13a1ca !important;
      cursor: pointer;
      background: none;
      -webkit-border-radius: 18px;
         -moz-border-radius: 18px;
              border-radius: 18px;
}
.wrapcards .box.add a {
        font-size: 16px;
        margin-top: 75px;
        margin-left: 110px;
        display: block;
        color: #13a1ca;
}
.wrapcards .box h2 {
      margin: 18px auto 20px;
      font-weight: normal;
      color: #FFFFFF;
      font-size: 16px;
      padding: 15px 20px;
      border-bottom: solid 1px #fff;
      display: block;
      font-width: bold;
}
.wrapcards .box .acctnumber, .wrapcards .box .acctname {
      font-weight: normal;
      color: #fff;
      font-size: 16px;
      margin-left: 18px;
      display: block;
      margin-top: 20px;
}



